
*{margin:0; padding: 0
}
body{
    font: 14px Arial, Helvetica, sans-serif
}
.time_bucket{
    height: 45px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
    background: #1D8CE0;
    color: #fff;
    margin-bottom: 3px
}
.time_bucket > div{
    line-height: 45px;
    padding: 0 10px
}
.time_bucket > div:active{
    background: #2c94e2
}
.time_bucket .none{
    pointer-events: none;
    color: #ccc
}



